<template>
  <v-row align="center" class="list px-3 mx-auto">
    <v-col cols="12" sm="12" class="mt-4">
      <v-btn to="/add" color="success" class="mr-4 w-25"> Добавить урок </v-btn>
    </v-col>

    <v-col cols="12" sm="12">
      <v-card class="mx-auto" tile>
        <v-data-table
          :headers="headers"
          :items="tutorials"
          disable-pagination
          :hide-default-footer="true"
        >
          <template v-slot:[`item.actions`]="{ item }">
            <v-icon small class="mr-2 editIcon" @click="editData(item.id)"
              >mdi-pencil</v-icon
            >
            <v-icon small class="delIcon" @click="deleteData(item.id)"
              >mdi-delete</v-icon
            >
          </template>
        </v-data-table>
      </v-card>
    </v-col>
    <v-snackbar v-model="snackbar" rounded="pill" :timeout="timeout">
      {{ message }}
    </v-snackbar>
  </v-row>
</template>

<script>
import axios from "axios";
import authHeader from '../services/auth-header';
export default {
  name: "timetibleList",
  data() {
    return {
      tutorials: [],
      title: "",
      headers: [
        { text: "Предмет", align: "start", value: "subject", sortable: true },
        { text: "Кабинет", value: "cabinet", sortable: true },
        { text: "Начало", value: "timeStart", sortable: false },
        { text: "Конец", value: "timeEnd", sortable: false },
        { text: "Четная", sortable: false, value: "isEven" },
        { text: "День недели", value: "weekDay", sortable: false },
        { text: "Преподаватель", value: "teacher", sortable: false },
        { text: "Actions", value: "actions", sortable: false },
      ],
      message: null,
      snackbar: false,
      timeout: 3000,
    };
  },
  created: function () {
    this.get();
  },
  methods: {
    get() {
      axios
        .get("api/timetable?token=" + authHeader() )
        .then((response) => {
          this.tutorials = response.data.map(this.getDisplayData);
        })
        .catch((error) => console.log(error));
    },

    editData(id) {
      this.$router.push({ name: "add", params: { id: id } });
    },

    deleteData(id) {
      axios
        .delete("api/timetable/" + id +"?token=" + authHeader())
        .then((response) => {
          this.message = response.data.message;
          this.snackbar = true;
          this.get();
        })
        .catch((error) => console.log(error));
    },

    getDisplayData(data) {
      return {
        id: data.id,
        isEven: data.isEven ? "Да" : "Нет",
        weekDay: data.weekDay,
        timeStart: data.timeStart,
        timeEnd: data.timeEnd,
        subject: data.subject,
        teacher: data.teacher,
        cabinet: data.cabinet,
      };
    },
  },
};
</script>

<style>
.list {
  max-width: 1280px;
}
.editIcon:hover {
  color: rgb(27, 122, 27);
}
.delIcon:hover {
  color: red;
}
</style>